.toggle-button
{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 55px;
    width: 65px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 1;
    box-sizing:border-box;
}
.toggle-button:focus
{
    outline: none;
}
.toggle-button__line
{
    width: 54px;
    height: 5px;
    background: #0a0a0a;
}