.header
{
    position: sticky;
    top: 0;
    left: 0;
    width: 100%;
    background: #7002ff;
    height: 100px;
    box-shadow: 0px 0px 4px 0px grey ;
    z-index: 9999;
    
}
.header_navi
{
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0 1rem;
    font-size: 25px;
}
.header_logo
{
    margin-left: 2rem;
    
    
    
}
.header_logo a
{
    margin-left: 2rem;
    color:#0a0a0a;

}
.header_logo a:hover,
.header_logo a:active
{
    text-shadow: -0.1px 0 #0a0a0a, 0 0.1px #0a0a0a, 0.1px 0 #0a0a0a, 0 -0.1px #0a0a0a;
}
.header_item ul
{
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}
.header_item li
{
    padding: 0 0.5rem;
    
}
.header_item li a
{
    text-decoration: none;
    color:White;
    font-weight: 700;
}

.header_item a:hover,
.header_item a:active
{
    color: #0a0a0a;
}
.spacer
{
    flex: 1;
}

@media (max-width: 768px)
{
    .header_item
    {
        display: none;
    }
    .header_logo a
    {
        margin-left: 0
    }
    
}
@media (min-width: 769px)
{
    .header-toggle
    {
        display:none;
    }
    .header_logo
    {
        margin-left: 0;
    }
   
}